<template>
  <div>
    <h1>{{ msg }}</h1>
    <h5>¡¡Cualquiera que tenga un corazón tan recio como el tuyo será bienvenido!!</h5>
    <!--Cards con los juegos-->
    <div @click="showAlert()" v-if="estaActivo">
      <img id="img" src="../assets/carro.png" />
      <span id="amount">{{cartAmount}}</span>
    </div>
    <div class="container text-center">
      <b-card-group deck class="mx-2 my-4">
        <div v-for="(item, index) in game" :key="index">
          <b-card
            :title="item.nombre_juego"
            align="center"
            style="max-width: 20rem"
            class="mx-2 my-4"
          >
            <b-card-text>{{item.desarrollador_juego}}</b-card-text>
            <b-card-text>{{item.fechaSalida}}</b-card-text>
            <b-card-text>{{item.genero}}</b-card-text>
            <b-card-text>Precio unitario: {{item.precio}}</b-card-text>
            <b-button @click="addToCart(item.nombre_juego)">Añadir al carrito de compras</b-button>
          </b-card>
        </div>
      </b-card-group>
    </div>
  </div>
</template>

<script>
import { mapActions, mapGetters, mapMutations } from "vuex";
export default {
  name: "home",
  props: {
    msg: String,
  },
  data() {
    return {
      cart: [],
      game: [],
      listGame: {
        nombre_juego: "",
        desarrollador_juego: "",
        genero: [],
        PEGI: "",
        fechaSalida: "",
      },
    };
  },
  created() {
    this.listarGame();
    this.leerToken();
  },
  computed: {
    ...mapGetters(["estaActivo", "siMostrar"]),
    cartAmount() {
      return this.cart.length;
    },
  },
  methods: {
    ...mapMutations(["obtenerUsuario", "obtenerRol"]),
    ...mapActions(["leerToken"]),
    listarGame() {
      this.axios
        .get("/game")
        .then((res) => {
          this.game = res.data;
        })
        .catch((e) => {
          console.log(e);
        });
    },
    addToCart(item) {
      this.cart.push(item);
    },
    mostrarJuegos() {
      alert(this.cart);
    },
    showAlert() {
      if (this.cart == 0) {
        this.$swal("No has añadido algun juego", Text= 'Añade algunos juegos!');
      } else {
        this.$swal("Juegos añadidos: ", (Text = `${this.cart}`));
      }
    },
  },
};
</script>

<style>
#amount {
  color: #f08804;
  position: absolute;
}
</style>